import React from "react";
import { graphql, Link } from "gatsby";
import { useTranslation } from "react-i18next";
import Layout from "./layout";
import { Post, Company } from "../interfaces";
import data from "../data/data.json";
import { format } from "date-fns";
import { es, fr, enUS, sl } from "date-fns/locale";
import images from "../images";

const info: Company = data[0];

type Props = {
  data: {
    allPostsJson: {
      nodes: Post[];
    };
  };
};

const PostPage: React.FC<Props> = ({ data }) => {
  const posts = data.allPostsJson.nodes;

  const { t, i18n } = useTranslation();
  const getLocale = (language: string) => {
    switch (language) {
      case "es":
        return es;
      case "fr":
        return fr;
      default:
        return enUS;
    }
  };

  return (
    <Layout data={info}>
      <h1 className="text-3xl text-center my-4">{t("blog.title")}</h1>
      <ul className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
        {posts.map((post) => (
          <li
            className="text-center border-2 bg-darkBg/50 rounded-lg p-2 flex-1 flex flex-col"
            key={post.slug}
          >
            <Link to={`/blog/${post.slug}`}>
              <h2>{t(`blog.${post.slug}-title`)}</h2>
              <p className="text-sm text-secondary">
                {format(new Date(post.date), "MMMM dd, yyyy", {
                  locale: getLocale(i18n.language),
                })}
              </p>
              <img
                className="thumbnail mx-auto my-4"
                src={images[post.slug as keyof typeof images].src}
                alt={images[post.slug as keyof typeof images].alt}
              />
            </Link>
          </li>
        ))}
      </ul>
    </Layout>
  );
};

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
    allPostsJson {
      nodes {
        title
        slug
        date
      }
    }
  }
`;

export default PostPage;
